:root {
  --main-color: #5865f2;
  --secondary-color: #98aae7;
  --secondary-color-r: 114;
  --secondary-color-g: 137;
  --secondary-color-b: 218;
  --background-color: #282a2e;
  --secondary-background-color: #2d2f33;
  --accent-background-color: #252629;
  --third-background-color: #202225;
  --box-background-color: #35353a;
}

.stats {
  width: 95%;
  margin: 0 auto;
  padding-top: 35px;
  padding-bottom: 60px;
}

.inner-card {
  padding: 10px;
}

.card {
  background: var(--third-background-color);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 25px;
}

.card p {
  margin: 0;
}

.statsNumbers {
  text-align: left;
  margin: 23px 5px;
  display: flex;
}

.statsNumbers h5 {
  text-align: center;
  margin-left: 10px;
  line-height: 35px;
}

.chart {
  height: 100%;
}

